@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.anim.shrink > * {
  opacity: 1;
}

.anim.fadeintoleft > .show {
  animation-name: fadeintoleft;
}

@keyframes shrink {
  from {
    transform: scale(1.1);
  }

  to {
    transform: scale(1);
  }
}

.anim.shrink > .show {
  animation-name: shrink;
}

@keyframes fadeintoright {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }

  to {
    opacity: 1;
  }
}

.anim.fadeintoright > .show {
  animation-name: fadeintoright;
}

@keyframes fadeintoleft {
  from {
    opacity: 0;
    transform: translateX(50px);
  }

  to {
    opacity: 1;
  }
}

.anim.fadein > .show {
