.cls-1 {
  fill: #0000;
  stroke: #fff;
}

path {
  stroke-dasharray: 50;
  opacity: 0;
  animation-name: draw2, fill;
  animation-duration: 6s, 1s;
  animation-timing-function: ease, ease-out;
  animation-iteration-count: 1, 1;
  animation-direction: normal, normal;
  animation-play-state: running, running;
  animation-delay: 0s, 1s;
  animation-fill-mode: forwards;
}

path:first-child {
  stroke-dasharray: 400;
  opacity: 1;
  animation-name: draw, fill;
  animation-duration: 6s, 1s;
  animation-timing-function: ease, ease-out;
  animation-iteration-count: 1, 1;
  animation-direction: normal, normal;
  animation-play-state: running, running;
  animation-delay: 0s, 1s;
  animation-fill-mode: forwards;
}

@keyframes fill {
  to {
    fill: #10adf3;
  }
}

@keyframes draw {
  to {
    stroke-width: 1px;
    opacity: 1;
    stroke-dasharray: 3000;
    stroke: #10adf3;
  }
}

@keyframes draw2 {
  to {
    stroke-width: 1px;
    opacity: 1;
    stroke-dasharray: 1000;
    stroke: #10adf3;
  }
}

