* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

img.bg {
  position: absolute;
  right: 10px;
  top: 40vh;
}

:root {
  --header-color: white;
  --header-bg: transparent;
  /* --footer-bg: #222;
    --footer-color: white; */
  --body-color: white;
  --body-bg: #000;
  --primary-color: rgb(92, 5, 255);
  --primary-color-hover: rgb(55, 7, 145);
  --secondary-color: orange;
  --secondary-color-hover: #ef7300;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: lighter;
}

body {
  /* font-family: 'Dosis', sans-serif; */
  font-family: "Raleway", sans-serif;
  background-color: var(--body-bg);
  color: var(--body-color);
}

a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
}

.btn {
  display: inline-block;
  background-color: white;
  color: #111;
  padding: 0.5rem 1.5rem;
  font-size: 0.9rem;
  border: none;
  cursor: pointer;
  border-radius: 0.25rem;
  transition-duration: 200ms;
}

.btn:hover {
  /* background-color: var(--primary-color-hover); */
  opacity: 0.8;
}

.btn.secondary {
  background-color: var(--secondary-color);
  color: white;
}

.btn.secondary:hover {
  background-color: var(--secondary-color-hover);
  color: white;
}

.btn.primary {background: rgb(113,6,236);
  background: linear-gradient(90deg, rgba(113,6,236,1) 0%, rgba(219,0,189,1) 63%, rgba(132,0,255,1) 100%);

  border-radius: 21px;
  -moz-border-radius: 21px;
  -webkit-border-radius: 21px;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#0063FF', endColorstr='#BF00FF', GradientType=1);
  transition-duration: 500ms;
  color: white;
  border-radius: 100rem;
}

.btn.primary:hover {
  /* background-color: var(--primary-color-hover); */
  background: -moz-linear-gradient(
    0% 50% 0deg,
    rgba(191, 0, 255, 1) 0%,
    rgba(0, 99, 255, 1) 100%
  );
  background: -webkit-linear-gradient(
    0deg,
    rgba(191, 0, 255, 1) 0%,
    rgba(0, 99, 255, 1) 100%
  );
  background: -webkit-gradient(
    linear,
    0% 50%,
    100% 50%,
    color-stop(0, rgba(191, 0, 255, 1)),
    color-stop(1, rgba(0, 99, 255, 1))
  );
  background: -o-linear-gradient(
    90deg,
    rgba(191, 0, 255, 1) 0%,
    rgba(0, 99, 255, 1) 100%
  );
  background: -ms-linear-gradient(
    90deg,
    rgba(191, 0, 255, 1) 0%,
    rgba(0, 99, 255, 1) 100%
  );
  -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr='#BF00FF', endColorstr='#0063FF' ,GradientType=0)";
  background: linear-gradient(
    90deg,
    rgba(191, 0, 255, 1) 0%,
    rgba(0, 99, 255, 1) 100%
  );
  border-radius: 21px;
  -moz-border-radius: 21px;
  -webkit-border-radius: 21px;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#BF00FF', endColorstr='#0063FF', GradientType=1);
  opacity: 0.9;
  color: white;
}

.container {
  max-width: 1000px;
  margin: auto;
  position: relative;
  padding: 0rem 1rem;
}

.spacer {
  height: 2.5rem;
}

.spacer.half {
  height: 1.25rem;
}

.title {
  margin-bottom: 2rem;
  font-size: 2.5em;
}

.blog {
  background-color: white;
  padding: 2rem;
  font-size: 1.5rem;
  text-align: center;
  color: black;
}

.blog a {
  text-decoration: underline;
}

