.anim > *.show {
  animation-duration: 500ms;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
}

.anim > * {
  /* opacity: 0; */
}

.btn + .btn {
  margin-left: 0.5rem;
}

.btn {
  border-radius: 100rem;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.anim.shrink > * {
  opacity: 1;
}

.anim.fadeintoleft > *.show {
  animation-name: fadeintoleft;
}

@keyframes shrink {
  from {
    transform: scale(1.1);
  }
  to {
    transform: scale(1);
  }
}

.anim.shrink > *.show {
  animation-name: shrink;
}

@keyframes fadeintoright {
  from {
    transform: translateX(-50px);
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.anim.fadeintoright > *.show {
  animation-name: fadeintoright;
}

@keyframes fadeintoleft {
  from {
    transform: translateX(50px);
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.anim.fadein > *.show {
  animation-name: fadein;
}

