@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

nav.main {
  --hover-bg: #fff;
  --hover-color: black;
  --select-color: white;
  --select-bg: var(--primary-color);
  --group-width: 200px;
  --padding: 0.5rem 1rem;
  --group-delay: 0.3s;
  position: fixed;
  padding: 0.5rem;
  top: 0%;
  font-weight: 200;
  z-index: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  transition-duration: 300ms;

  ul {
    display: flex;
    align-items: center;
    font-size: 1rem;
    width: 100%;
    transition-duration: 300ms;
  }

  &.scrolled {
    background: #111;
    /* position: sticky; */

    ul {
      width: 466px;
    }
  }

  li {
    display: flex;
    flex-direction: column;
    padding: 0rem;

    a {
      color: var(--color);
      padding: 0.5rem 0rem;
      transition-duration: 100ms;
      text-align: center;
      border-radius: 100rem;
      width: 150px;
      opacity: 0.8;
    }

    &:hover a {
      /* background-color: var(--hover-bg);
      color: var(--hover-color); */
      opacity: 1;
      font-weight: 400;
    }

    &.selected a,
    &.selected:hover a {
      background: rgb(113, 6, 236);
      background: linear-gradient(90deg, rgba(113, 6, 236, 1) 0%, rgb(168, 0, 219) 63%);
      border-radius: 21px;
      -moz-border-radius: 21px;
      -webkit-border-radius: 21px;
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00BFFF', endColorstr='#847FFF', GradientType=1);
      color: var(--select-color);
      font-weight: initial;
      opacity: 1;
      animation: fadeIn 0.3s ease-in-out; // Add animation here
    }
  }
}
